.chat{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 8px;
    border-bottom: 1px solid whitesmoke;
    cursor: pointer;
}
.chat:hover{
    opacity: 0.8;
}
.chat_avatar{
    height: 35px;
    width: 35px;
}
.chat_readIcon{
    color: red;
}
.chat_info{
    padding-left: 5px;
    flex: 1;
}
.chat_info > h4{
    font-size: 11px;
    font-weight: 500;

} 
.chat_info > p{
    font-size: 9px;
} 